import React from "react";
import {userInfoAC} from '../redux/reducer'
import { useDispatch} from "react-redux";
import styles from "../styles/Table.module.scss"
import classNames from 'classnames'


export const TableRow = ({users}) => {

    const[isActiveClass,setIsActiveClass]=React.useState(false)
    const dispatch=useDispatch()

    let {id, firstName, lastName, email, phone} = users
    const userInfo = () => {
        dispatch(userInfoAC(true, id))
    setIsActiveClass(true)
    }
    return <tr className={classNames(styles.tableRow,isActiveClass && styles.active)}  onClick={userInfo}>
        <th>{id}</th>
        <th>{firstName}</th>
        <th>{lastName}</th>
        <th>{email}</th>
        <th>{phone}</th>
    </tr>
}

